<template>
    <div class="offline-map">
        <navigate title="离线地图" position="absolute"/>
        <div class="offline-map-container scroll-wrapper">
            <div>
                <div class="title">下载离线地图,节省90%流量!</div>
                <div class="tips">(推荐在无线网络环境下载,其他环境下载可能会产生数据费用)</div>
            </div>
            <template v-for="(item, index) in list">
                <div class="div-container">
                    <mu-row>
                        <mu-col span="9" class="text-container">
                            <div class="div-container-title">{{item.name}}</div>
                            <div class="div-container-desc">{{item.detail}}</div>
                        </mu-col>
                        <mu-col span="3" class="div-flex icon-container" @click="handlerDownload(item)">
                            <div class="circular-progress-wrapper" v-if="item.style === 'loading'">
                                <mu-circular-progress mode="determinate" color="#4caf50"
                                                      :value="item.md5" :size="26"/>
                            </div>
                            <div class="icon-wrapper" v-else>
                                <mu-icon :value="thatIcon(item)" :color="thatColor(item)" size="32"/>
                            </div>
                            <div class="icon-container-title">{{thatStatus(item)}}</div>
                            <div class="icon-container-desc">{{(item.size/(1024*1024)).toFixed(2)}}M</div>
                            <div v-show="false">{{initStatus(item)}}</div>
                        </mu-col>
                    </mu-row>
                </div>
                <div style="height:20px;"></div>
            </template>
            <!--<div class="div-container">-->
            <!--<mu-row>-->
            <!--<mu-col span="3" class="div-flex">-->
            <!--<mu-icon value="check_circle" color="#74b000" size="32"></mu-icon>-->
            <!--<div class="icon-container-title">继续</div>-->
            <!--<div class="icon-container-desc">42.4M</div>-->
            <!--</mu-col>-->
            <!--<mu-col span="3" class="div-flex">-->
            <!--<mu-icon value="check_circle" color="#3674b3" size="32"></mu-icon>-->
            <!--<div class="icon-container-title">继续</div>-->
            <!--<div class="icon-container-desc">42.4M</div>-->
            <!--</mu-col>-->
            <!--<mu-col span="3" class="div-flex">-->
            <!--<div class="circular-progress-wrapper">-->
            <!--<mu-circular-progress mode="determinate" :value="78" :size="26"-->
            <!--color="#74b000"/>-->
            <!--</div>-->
            <!--<div class="icon-container-title">78%</div>-->
            <!--<div class="icon-container-desc">42.4M</div>-->
            <!--</mu-col>-->
            <!--<mu-col span="3" class="div-flex">-->
            <!--<mu-icon value="pause_circle_outline" size="32"></mu-icon>-->
            <!--<div class="icon-container-title">更新</div>-->
            <!--<div class="icon-container-desc">42.4M</div>-->
            <!--</mu-col>-->
            <!--</mu-row>-->
            <!--</div>-->
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {downloadMapList, downloadMapUrl, downloadMap} from "../../../api/sys"
    import {
        downloadFile,
        downloadFilePause,
        downloadFileSatus,
        downloadFileClear,
        getSavePath
    } from "../../../common/js/cordova/download"

    export default {
        name: "offlineMap",
        data() {
            return {
                params: {
                    pageNumber: 1,
                    pageSize: 10
                },
                list: []
            }
        },
        activated() {
            this.downloadMapList()
            downloadFile("", "", (successData) => {
                    console.log(successData)
                }, (errorData) => {
                    console.log(errorData)
                }
            )
        },
        methods: {
            downloadMapList() {
                downloadMapList(this.params).then(res => {
                    this.list = res
                })
            },
            handlerDownload(item) {
                switch (item.style) {
                    case 'loading':
                        this.pause(item)
                        return
                    case 'update':
                        return
                    default:
                        this.download(item)
                        return
                }
            },
            download(item) {
                if (item.style === "done") {
                    return
                }
                // const url = this.$images + item.file_path
                if (!item.file_path) {
                    return this.$toast('无效下载地址');
                }

                item.style = 'loading'

                const url = item.file_path

                // 文件存储路径
                const savePath = getSavePath() + item.name + '.vtpk'

                // 下载文件地址存储到指定路径
                downloadFile(url, savePath, (success) => {
                    console.log(success)
                    let data = JSON.parse(success.toString())
                    if (data.type === "completed") {
                        item.style = "done"
                    } else if (data.type === "progress") {
                        item.id = data.id
                        item.md5 = (Math.round(parseInt(data.soFarBytes) / parseFloat(data.totalBytes) * 10000) / 100).toFixed(0)
                    }
                }, (error) => {

                })
            },
            pause(item) {
                item.style = 'pause'

                downloadFilePause(item.id)
            },
            downloadFileStatus(item) {
                // const url = this.$images + item.file_path
                if (!item.file_path) {
                    return
                }

                const url = item.file_path

                const savePath = getSavePath() + item.name + '.vtpk'

                downloadFileSatus(url, savePath, (success) => {
                    console.log(success)

                    let data = JSON.parse(success.toString())

                    if (data.status === -2) {
                        item.style = 'pause'
                    } else if (data.status === -3) {
                        if (data.soFarBytes === data.totalBytes) {
                            item.style = 'done'
                        }
                    }
                })
            },
            downloadFileClear(item) {

            },
            initStatus(item) {
                this.downloadFileStatus(item)
                return ''
            },
            thatStatus(item) {
                switch (item.style) {
                    case 'loading':
                        return '下载中'
                    case 'update':
                        return '更新'
                    case 'pause':
                        return '暂停'
                    case 'done':
                        return '完成'
                    default:
                        return '下载'
                }
            },
            thatIcon(item) {
                switch (item.style) {
                    case 'update':
                        return 'sync'
                    case 'pause':
                        return 'pause_circle_outline'
                    case 'done':
                        return 'done'
                    default:
                        return 'arrow_downward'
                }
            },
            thatColor(item) {
                switch (item.style) {
                    case 'download':
                        return '#03a9f4'
                    case 'update':
                        return '#4caf50'
                    case 'done':
                        return '#4caf50'
                    case 'pause':
                        return '#03a9f4'
                    default:
                        return '#ffeb3b'
                }
            }
        },
        components: {
            Navigate
        },
        computed: {}
    };
</script>

<style lang="stylus">
    @import "../../../common/stylus/variable.styl"

    .offline-map
        padding-top 46px
        background-color #EFEFEF

        > .offline-map-container
            width 100%
            height 100%

            .title
                padding 15px 15px 10px
                font-size $font-size-lm
                opacity 0.75

            .tips
                padding 0 15px 15px 15px
                line-height 1.78571
                font-size $font-size-ms
                opacity 0.75

            .div-container
                padding 15px 0 15px 15px
                background #ffffff

            .div-container-title
                color #000000
                font-size $font-size-ls

            .div-container-desc
                padding-top 10px
                line-height 1.78571
                font-size $font-size-ms

            .icon-container-title
                font-size $font-size-ms

            .icon-container-desc
                font-size $font-size-sl

            .div-flex
                display flex
                flex-direction column
                align-items center
                justify-content space-between
                height 75px

            .circular-progress-wrapper
                display flex
                align-items center
                justify-content center
                width 32px
                height 32px
                overflow hidden

            .text-container
                padding-right 20px

            .icon-container
                border-left 1px solid #F8F8F8
</style>



